// Slowly increase opacity of the card
@keyframes fade-in{

    0%{ opacity: 0; }
    100%{ opacity: 1; }

}

// Change the gradient of the colors assuming the background size is big enough
@keyframes gradient{

    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
}

// Pulse hover effect
@keyframes pulse{

    0%{
        transform: scale(1);
    }
    50%{
        transform: scale(1.05);
    }
    100%{
        transform: scale(1);
    }

}

// Standard transition
$standard_transition: all 0.15s linear;